@import '../../../../common/styles/mixins';

@import '../../../../common/styles/mixins';

.innerWrapper {
  padding-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;

  .wrapper{

    width: 70%;
    word-break: break-word;

    .title {
      font-weight: 600;
      font-size: 22px;
      line-height: 27px;
    }

  }

  .btn {
    padding: 10px 15px;
    font-size: 16px;
    background: #212121;
    box-shadow: 0px 4px 18px rgba(27, 27, 28, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    border-radius: 30px;
    border: none;
    color: #fff;
    @include btn;
  }

}